.profile__name{
    margin: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 1.14;
    grid-area: name;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 768px) {
    .profile__name{
        font-size: 27px;
        line-height: 1.21;
    }
}

@media screen and (max-width: 576px) {
    .profile__name{
        margin-left: 32px;
    }
}